import * as React from 'react';
import Button, { ButtonProps } from '@material-ui/core/Button';
import { useDispatch } from '../utils/AppStateContext';

interface Props extends ButtonProps {
  counterAction: 'INCREMENT' | 'DECREMENT' | 'RESET_COUNT';
}

export default function CounterButton(props: Props) {
  const dispatch = useDispatch();
  const { counterAction } = props;
  const _onClick = React.useCallback(() => {
    dispatch({ type: counterAction });
  }, [dispatch, counterAction]);

  return (
    <Button {...props} onClick={_onClick}>
      {props.children}
    </Button>
  );
}
